body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', 'Courier', monospace, monospace;
  background: -webkit-linear-gradient(left, #a5c956 0%, #9eebcf 100%);
  background: linear-gradient(90deg, #a5c956 0%, #9eebcf 100%);
}

button {
  cursor: pointer;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.centerFlex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -11;
}
.Tilt{
    background: -webkit-linear-gradient(288deg, #9eebcf 0%, #a5c956 100%);
    background: linear-gradient(0.45turn, #9eebcf 0%, #a5c956 100%);
}
.form {
    width: 500px;
    background:
            -webkit-linear-gradient(45deg, #92baac 45px, transparent 45px)64px 64px,
            -webkit-linear-gradient(45deg, #92baac 45px, transparent 45px,transparent 91px, #e1ebbd 91px, #e1ebbd 135px, transparent 135px),
            -webkit-linear-gradient(135deg, #92baac 23px, transparent 23px, transparent 68px,#92baac 68px,#92baac 113px,transparent 113px,transparent 158px,#92baac 158px);
    background:
            linear-gradient(45deg, #92baac 45px, transparent 45px)64px 64px,
            linear-gradient(45deg, #92baac 45px, transparent 45px,transparent 91px, #e1ebbd 91px, #e1ebbd 135px, transparent 135px),
            linear-gradient(-45deg, #92baac 23px, transparent 23px, transparent 68px,#92baac 68px,#92baac 113px,transparent 113px,transparent 158px,#92baac 158px);
    background-color:#e1ebbd;
    background-size: 128px 128px;
}
.bounding-box {
    position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
}
